/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 15px;
}

/*------------------------------*/
/* main-hero */
/*-----------------------------*/
.main-hero {
  height: 100vh;
  min-height: 700px;
  width: 100%;
  background-attachment: fixed;
  background-color: #b4a39d;
  background-image: url("../assets/herobanner.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
}

/*------------------------------*/
/* .page-header */
/*-----------------------------*/
.page-header {
  width: 100%;
  min-height: 100px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-nav-list {
  display: flex;
  justify-content: space-between;
}

.page-nav-list li {
  padding: 0 30px;
}

.page-nav-list li:nth-of-type(2) {
  padding: 0 100px 0 0;
}

.page-nav-list li:nth-of-type(4) {
  padding: 0 0 0 300px;
}

.page-nav-list a {
  color: #000;
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  font-family: 'Karla', sans-serif;
}

@media (max-width: 800px) {
  .page-nav-list {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-left: 180px;
  }
  .page-nav-list li {
    padding: 0;
  }
  .page-nav-list li:nth-of-type(2) {
    padding: 0;
  }
  .page-nav-list li:nth-of-type(4) {
    padding: 0;
  }
  .header-logo {
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
  }
}

.header-logo {
  width: 200px;
  height: 100px;
  position: absolute;
  display: flex;
}

/*------------------------*/
/* main-banner */
/*------------------------*/
.main-banner {
  margin-top: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 760px) {
  .main-banner {
    text-align: center;
    margin-top: 50px;
  }
}

.main-banner div {
  background-color: #fff;
  width: 20em;
  height: 100px;
  position: relative;
}

.main-banner h1 {
  font-size: 4.5em;
  color: #000;
  position: absolute;
  bottom: +50%;
}

/*------------------------------------*/
/* main-offer */
/*------------------------------------*/
.main-article {
  display: flex;
  flex-direction: column;
}

.main-article-title {
  font-size: 60px;
  text-transform: lowercase;
  font-family: 'Libre Baskerville', serif;
  padding: 50px 0 30px 0;
}

.main-article-title:after {
  content: "";
  display: inline-flex;
  height: 20px;
  width: 220px;
  background-color: #ECECEC;
  margin-left: 20px;
}

/* font-family: 'Libre Baskerville', serif;
font-family: 'Karla', sans-serif; */
.main-offer-section {
  position: relative;
}

.main-article-text {
  /* font-family: 'Karla', sans-serif; */
  padding: 20px 0;
  width: 70%;
}

.offer-box {
  font-family: 'Karla', sans-serif;
  text-transform: uppercase;
  display: flex;
}

.list {
  padding: 20px 0 20px 0;
}

.list li {
  padding: 10px 20px 10px 0;
  margin: 0 20px 0 0;
  border-bottom: 1px solid #ECECEC;
}

.list-title {
  font-weight: bold;
  padding-top: 20px;
}

.image-cnt {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 73%;
  position: absolute;
  z-index: 1000;
  width: 10px;
  height: 10px;
}

.image-title {
  font-family: 'Karla', sans-serif;
  text-transform: uppercase;
  font-size: 25px;
}

.image-cnt img {
  width: 200px;
  position: relative;
  z-index: -1000;
}

.image-title::after {
  content: "";
  color: black;
  width: 150px;
  height: 150px;
  border: 20px solid #D7D7D7;
  position: absolute;
  border-radius: 50%;
  left: 20%;
  bottom: 50%;
}

@media (max-width: 760px) {
  .image-cnt {
    display: none;
  }
}

/*-----------------------------*/
/* main-about-article */
/*-----------------------------*/
.main-about-article {
  background-image: url("../assets/about-bg.jpg");
  background-repeat: no-repeat;
  width: 100%;
  min-height: 90vh;
  background-position: center;
  margin-top: 20px;
}

.main-about-article .main-article-title:after {
  background-color: #fff;
}

/*-----------------------------*/
/* main-contact-article */
/*-----------------------------*/
.main-contact-form .container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  width: 80px;
}

@media (max-width: 760px) {
  .main-contact-form .container {
    flex-direction: column;
  }
}

.main-contact-form {
  font-size: 20px;
  color: #000;
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 100%;
}

.adress-cnt {
  display: flex;
  flex-direction: column;
  padding: 50px;
}

.main-contact-form label p {
  color: #D9D9D9;
}

input {
  background: transparent;
  border: none;
  border-bottom: 2px solid #D9D9D9;
  width: 300px;
  height: 30px;
  outline: none;
  font-size: 20px;
  color: #574f4f;
}

.main-article-btn {
  color: #000;
  background-color: #D7D7D7;
  text-decoration: none;
  width: 300px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  outline: none;
}

.adress-cnt figure {
  margin-bottom: 50px;
  font-family: 'Karla', sans-serif;
  position: relative;
  text-transform: uppercase;
}

.adress-cnt figure:after {
  content: "";
  color: black;
  width: 150px;
  height: 150px;
  border: 20px solid #D7D7D7;
  position: absolute;
  border-radius: 50%;
  right: 70%;
  bottom: -20%;
  z-index: -1;
}

@media (max-width: 760px) {
  .adress-cnt figure,
  .adress-cnt figure:after {
    display: none;
  }
}

.adress {
  font-family: 'Karla', sans-serif;
}

.adress li:first-child {
  font-weight: bold;
  text-transform: uppercase;
  padding: 0 0 20px 0;
}

/*-----------------------------*/
/* page-footer */
/*-----------------------------*/
.page-footer {
  width: 100%;
  height: 200px;
  background-image: url("../assets/footer.jpg");
  background-size: 3000px 200px;
  background-repeat: no-repeat;
  background-position: center top;
  display: flex;
  align-items: center;
}

.icons-list {
  display: flex;
}

.icons-list img {
  width: 2em;
  height: 2em;
}
